.header {
	position: fixed;
	z-index: 5;
	width: 100%;
	top: 0;
	color: var(--color-white);
	background-color: #11111100;
	// background-color: var(--color-dark);
	// box-shadow: 0 0 10px rgba(255,255,255,0.5);

	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		// background-color: var(--color-black);
	}

	&__container {
		max-width: 1180px;
		margin: 0 auto;
		padding: 0 10px;
		// background-color: var(--color-dark);
	}

	&__body {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		height: 64px;
		align-items: center;
		.phone {
			font-size: 18px;
			&:before {
				content: "";
				position: absolute;
				height: 20px;
				width: 20px;
				top: 23px;
				margin-left: -27px;
				background: url(../img/phone.svg) no-repeat;
			}
		}
	}

	&__burger {
		display: none;
	}
	// &__menu {
	// 	background-color: var(--color-black);
	// 	height: 100%;
	// }

	&__list {
		display: flex;

		.active-link {
			&:hover {
				&:after {
					height: 1px;
					bottom: 0px;
				}
			}
			&:after {
				content: "";
				position: absolute;
				height: 1px;
				width: 100%;
				background-color: var(--color-white);
				opacity: 1;
				bottom: 0px;
				left: 0;
				transition: all 0.4s;
			}
		}

		li {
			list-style: none;
			margin: 0 25px 0 0;
			cursor: pointer;
			&:last-child {
				margin: 0;
			}
		}
	}

	&__link {
		// color: var(--color-dark);
		position: relative;
		text-transform: uppercase;
		font-size: 16px;
		text-decoration: none;

		&:hover {
			color: var(--color-white);
			transition: all 0.4s;
			&:after {
				content: "";
				position: absolute;
				height: 2px;
				width: 100%;
				background-color: var(--color-white);
				opacity: 1;
				bottom: -2px;
				left: 0;
				transition: all 0.4s;
			}
		}

		&:after {
			content: "";
			position: absolute;
			height: 1px;
			width: 100%;
			background-color: var(--color-white);
			opacity: 0;
			bottom: 2px;
			left: 0;
			transition: all 0.4s;
		}
	}

	@media (max-width: 900px) {
		.header__container {
			background-color: var(--color-dark);
		}
		.header__link {
			&:after {
				height: 0px;
			}
		}
		.active-link {
			&:hover {
				&:after {
					height: 0px;
				}
			}
			&:after {
				height: 0px;
			}
		}

		& {
			//			height: 100%;
			z-index: 5;

			&__list {
				display: inline-block;
				li {
					font-size: 18px;
					margin: 0 0 20px 20px;
					&:last-child {
						margin: 0 0 0 20px;
					}
				}
			}

			&__link:after {
				height: 0;
			}

			&__burger {
				display: block;
				position: relative;
				width: 30px;
				height: 20px;
				z-index: 10;
				cursor: pointer;
			}

			&__burger span {
				background-color: var(--color-white);
				position: absolute;
				width: 100%;
				height: 2px;
				left: 0;
				top: 9px;
				transition: all 0.3s;
			}

			&__burger:before,
			&__burger:after {
				content: "";
				background-color: var(--color-white);
				width: 100%;
				position: absolute;
				height: 2px;
				left: 0;
				transition: all 0.3s;
			}

			&__burger:before {
				// width: 50%;
				top: 0;
			}

			&__burger:after {
				bottom: 0;
				// width: 100%;
			}

			&__menu {
				position: fixed;
				top: -100%;
				left: 0;
				width: 100%;
				height: 100%;
				transition: all 0.3s ease 0s;
				padding-top: 60px;
				//display: none;
			}
		}

		.menu__active:before {
			transform: rotate(45deg);
			top: 9px;
		}
		.menu__active:after {
			transform: rotate(-45deg);
			bottom: 9px;
		}
		.menu__active span {
			transform: scale(0);
		}

		.header__menu.menu__active {
			top: 0;
			background-color: var(--color-dark);
			z-index: 1;
		}
	}
}
