.container {
	color: var(--color-black);
	text-align: center;
	&__title {
		position: relative;
		margin-top: 100px;
		&-title {
			margin: 0 0 100px 0;
			padding: 0;
			font-size: 36px;
			text-transform: uppercase;
		}
		&:after {
			content: "";
			position: absolute;
			bottom: -50px;
			margin-left: -50px;
			width: 100px;
			height: 2px;
			z-index: 2;
			background-color: var(--color-blue);
		}
	}
	&__desc {
		margin: 50px 100px;
		font-family: "Roboto", sans-serif;
		&-desc {
			font-size: 28px;
		}
	}
	&__content {
		font-family: "Roboto", sans-serif;
		font-size: 20px;
		text-indent:40px;
		text-align: left;
		
	}
	.btn-link {
		font-family: "Roboto", sans-serif;
		font-size: 16px;
		width: 200px;
		height: 60px;
		color: var(--color-white);
		background-color: var(--color-blue);
		text-transform: uppercase;
		border-radius: 30px;
		display: inline-block;
		margin: 60px 0 0 0;
		line-height: 60px;
		outline: none;
		text-decoration: none;
	}
	
}

.image-slider__image {
	text-align: center;
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
		background-color: rgba(0, 0, 0, 0.5);
	}

}
.main__contant {
	position: absolute;
	display: flex;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	text-align: center;
	color: #fff;
	z-index: 2;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: center;
	&-title h1 {
		font-size: 68px;
	}
	&-desc h2 {
		font-size: 24px;
		line-height: 1;
		margin: 0;
		font-weight: 400;
	}
	&-desc h3 {
		font-family: "OpenSans", sans-serif;
		font-size: 24px;
		line-height: 1;
		margin: 50px 0 0 0;
		font-weight: 600;
	}
}
.image-slider__image img {
	// max-width: 100%;
	// width: 100vw;
	width: 150%;
	height: 100vh;
	object-fit: cover;

}

.slick-slider {
	z-index: -1;
}
.slick-active img {
	width: 100%;
	transition: all 0.5s;
}

@media (max-width: 1200px) {
	.main__contant {
		&-title h1 {
			font-size: 60px;
		}
	}
}

@media (max-width: 900px) {
	.main__contant {
		&-title h1 {
			font-size: 48px;
		}
	}
}
