html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  color: var(--color-black);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: auto;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1190px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: fixed;
  z-index: 5;
  width: 100%;
  top: 0;
  color: var(--color-white);
  background-color: #11111100;
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.header__container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 10px;
}

.header__body {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 64px;
  align-items: center;
}

.header__body .phone {
  font-size: 18px;
}

.header__body .phone:before {
  content: "";
  position: absolute;
  height: 20px;
  width: 20px;
  top: 23px;
  margin-left: -27px;
  background: url(../img/phone.svg) no-repeat;
}

.header__burger {
  display: none;
}

.header__list {
  display: flex;
}

.header__list .active-link:hover:after {
  height: 1px;
  bottom: 0px;
}

.header__list .active-link:after {
  content: "";
  position: absolute;
  height: 1px;
  width: 100%;
  background-color: var(--color-white);
  opacity: 1;
  bottom: 0px;
  left: 0;
  transition: all 0.4s;
}

.header__list li {
  list-style: none;
  margin: 0 25px 0 0;
  cursor: pointer;
}

.header__list li:last-child {
  margin: 0;
}

.header__link {
  position: relative;
  text-transform: uppercase;
  font-size: 16px;
  text-decoration: none;
}

.header__link:hover {
  color: var(--color-white);
  transition: all 0.4s;
}

.header__link:hover:after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: var(--color-white);
  opacity: 1;
  bottom: -2px;
  left: 0;
  transition: all 0.4s;
}

.header__link:after {
  content: "";
  position: absolute;
  height: 1px;
  width: 100%;
  background-color: var(--color-white);
  opacity: 0;
  bottom: 2px;
  left: 0;
  transition: all 0.4s;
}

@media (max-width: 900px) {
  .header .header__container {
    background-color: var(--color-dark);
  }
  .header .header__link:after {
    height: 0px;
  }
  .header .active-link:hover:after {
    height: 0px;
  }
  .header .active-link:after {
    height: 0px;
  }
  .header {
    z-index: 5;
  }
  .header__list {
    display: inline-block;
  }
  .header__list li {
    font-size: 18px;
    margin: 0 0 20px 20px;
  }
  .header__list li:last-child {
    margin: 0 0 0 20px;
  }
  .header__link:after {
    height: 0;
  }
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 10;
    cursor: pointer;
  }
  .header__burger span {
    background-color: var(--color-white);
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    top: 9px;
    transition: all 0.3s;
  }
  .header__burger:before, .header__burger:after {
    content: "";
    background-color: var(--color-white);
    width: 100%;
    position: absolute;
    height: 2px;
    left: 0;
    transition: all 0.3s;
  }
  .header__burger:before {
    top: 0;
  }
  .header__burger:after {
    bottom: 0;
  }
  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease 0s;
    padding-top: 60px;
  }
  .header .menu__active:before {
    transform: rotate(45deg);
    top: 9px;
  }
  .header .menu__active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  .header .menu__active span {
    transform: scale(0);
  }
  .header .header__menu.menu__active {
    top: 0;
    background-color: var(--color-dark);
    z-index: 1;
  }
}

.courses {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-indent: 0;
  font-family: Oswald, sans-serif;
  margin: 10px -5px;
}

.courses .course__card {
  display: flex;
  flex: 1 1 45%;
  margin: 10px 5px;
  max-width: 560px;
  flex-direction: column;
  justify-content: space-between;
}

.courses .course__card-photo {
  max-width: 560px;
  height: 340px;
}

.courses .course__card-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.courses .course__card-title {
  font-size: 24px;
  line-height: 1.35;
  font-weight: 600;
  margin: 25px 0;
}

.courses .course__card-desc {
  font-size: 14px;
}

.courses .course__card-btn {
  margin-top: 25px;
}

.courses .course__card-btnlink {
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  width: 100px;
  height: 35px;
  margin-top: 25px;
  color: var(--color-white);
  background-color: var(--color-red);
  text-transform: uppercase;
  border-radius: 5px;
  display: inline-block;
  margin: 0px 0 0 0;
  line-height: 35px;
  outline: none;
  text-align: center;
  text-indent: 0;
  text-decoration: none;
}

@media (max-width: 800px) {
  .courses .course__card {
    flex: 1 1 45%;
  }
  .courses .course__card-title {
    font-size: 20px;
    line-height: 1.35;
    font-weight: 600;
    margin: 25px 0;
  }
  .courses .course__card-desc {
    font-size: 14px;
  }
}

@media (max-width: 680px) {
  .courses .course__card {
    flex: 1 1 100%;
  }
  .courses .course__card-title {
    font-size: 20px;
    line-height: 1.35;
    font-weight: 600;
    margin: 25px 0;
  }
  .courses .course__card-desc {
    font-size: 14px;
  }
}

.teachers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-indent: 0;
  text-align: center;
  font-family: Oswald, sans-serif;
  margin: 10px -5px;
}

.teachers .teacher__card {
  display: flex;
  flex: 1 1 24%;
  margin: 10px 5px;
  max-width: 210px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.teachers .teacher__card-photo {
  max-width: 200px;
  height: 200px;
}

.teachers .teacher__card-photo img {
  border-radius: 100px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.teachers .teacher__card-title {
  font-size: 22px;
  line-height: 1.35;
  font-weight: 600;
  margin: 25px 0 15px 0;
}

.teachers .teacher__card-desc {
  font-size: 14px;
  color: var(--color-gray);
}

@media (max-width: 680px) {
  .teachers .teacher__card {
    flex: 1 1 50%;
  }
}

@media (max-width: 530px) {
  .teachers .teacher__card {
    flex: 1 1 100%;
    max-width: 100%;
  }
}

.sales {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-indent: 0;
  text-align: center;
  font-family: Oswald, sans-serif;
  margin: 10px -5px;
}

.sales .sale__card {
  display: flex;
  flex: 1 1 33%;
  margin: 10px 5px;
  max-width: 315px;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 9px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.sales .sale__card-title {
  font-size: 20px;
  font-weight: 600;
  margin: 45px 0 20px 0;
}

.sales .sale__card-desc {
  font-size: 14px;
  margin: 20px 0 30px 0;
  color: var(--color-gray);
}

.sales .sale__card-price {
  font-size: 38px;
  font-weight: 600;
}

.sales .sale__card-btn {
  margin-top: 35px;
}

.sales .sale__card-btnlink {
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  width: 100px;
  height: 35px;
  color: var(--color-white);
  background-color: var(--color-red);
  text-transform: uppercase;
  border-radius: 5px;
  display: inline-block;
  margin: 0 0 45px 0;
  line-height: 35px;
  outline: none;
  text-align: center;
  text-indent: 0;
  text-decoration: none;
}

.main__container {
  padding-top: 0;
  width: 100%;
  height: 100vh;
}

.container {
  color: var(--color-black);
  text-align: center;
}

.container__title {
  position: relative;
  margin-top: 100px;
}

.container__title-title {
  margin: 0 0 100px 0;
  padding: 0;
  font-size: 36px;
  text-transform: uppercase;
}

.container__title:after {
  content: "";
  position: absolute;
  bottom: -50px;
  margin-left: -50px;
  width: 100px;
  height: 2px;
  z-index: 2;
  background-color: var(--color-blue);
}

.container__desc {
  margin: 50px 100px;
  font-family: "Roboto", sans-serif;
}

.container__desc-desc {
  font-size: 28px;
}

.container__content {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  text-indent: 40px;
  text-align: left;
}

.container .btn-link {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  width: 200px;
  height: 60px;
  color: var(--color-white);
  background-color: var(--color-blue);
  text-transform: uppercase;
  border-radius: 30px;
  display: inline-block;
  margin: 60px 0 0 0;
  line-height: 60px;
  outline: none;
  text-decoration: none;
}

.image-slider__image {
  text-align: center;
}

.image-slider__image:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.5);
}

.main__contant {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  text-align: center;
  color: #fff;
  z-index: 2;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
}

.main__contant-title h1 {
  font-size: 68px;
}

.main__contant-desc h2 {
  font-size: 24px;
  line-height: 1;
  margin: 0;
  font-weight: 400;
}

.main__contant-desc h3 {
  font-family: "OpenSans", sans-serif;
  font-size: 24px;
  line-height: 1;
  margin: 50px 0 0 0;
  font-weight: 600;
}

.image-slider__image img {
  width: 150%;
  height: 100vh;
  object-fit: cover;
}

.slick-slider {
  z-index: -1;
}

.slick-active img {
  width: 100%;
  transition: all 0.5s;
}

@media (max-width: 1200px) {
  .main__contant-title h1 {
    font-size: 60px;
  }
}

@media (max-width: 900px) {
  .main__contant-title h1 {
    font-size: 48px;
  }
}
